import React from 'react';
import Loading from './Loading.gif';

export default ({ width }) => {
  return (
    <div>
      <img
        src={Loading}
        style={{ width: `${width}`, margin: 'auto', display: 'block', background: 'transparent' }}
        alt="Loading..."
      />
    </div>
  );
};



